</template>

<script>
  import common from '../../mixins/common'

  export default {
    mixins: [common],
    data() {
      return {
        name: 20,  //当前表格的名称编号
        setting: []
      }
    },
    computed: {
      formData() {
        return this.$store.state.tableData[this.name]
      }
    },
    mounted() {
      this.inputChangeEvent()
      this.inputClickEvent()
      this.getSetting()  //获取后台设置数据
    },
    methods: {
      getSetting() {
        if (this.formData) {
          this.recoverFormData()
        }
        // axios.get('getSetting').then(res => {})
        //获取预设数据
        // this.setting = {'1_1': 10, '2_1': 5}
        this.initData()
      },
      initData() {
        this.$Loading.start()
        let that = this
        for (let key in this.setting) {
          J('input').each(function () {
            if (J(this).attr('name') === key) {
              J(this).val((that.setting[key] * 1).toFixed(2))
              J(this).trigger('blur')
              return false
            }
          })
        }
        this.$Loading.finish()
      },
      recoverFormData() {
        this.$Loading.start()
        let that = this
        J('input').each(function () {
          let nm = J(this).attr('name')
          that.formData.map(v => {
            if (v.name === nm) {
              J(this).val(v.value)
            }
          })
        })
        this.$Loading.finish()
      },
      fn() {
        //第29行计算
        this.fn2()
        //第32行计算
        this.fn1(1, 35, [1])
      },
      // q 要运算的列
      // r 要运算多少行
      // jump 参与运算的列
      // 1:第1列运算
      fn1(q, r, jump) {
        var sum = 0
        for (let i = 1; i <= r; i++) {
          for (let k = 0; k < jump.length; k++) {
            let dom = J('input[name=' + i + '_' + jump[k] + ']')
            if (dom.val()) {
              if (i != 5 && i != 6 && i != 32 && i != 33) {
                if (i != 30) {
                  sum += parseFloat(dom.val())
                } else {
                  sum -= parseFloat(dom.val())
                }
              }
            }
          }
        }
        J('input[name=36_' + q + ']').val(sum.toFixed(2))
      },
      fn2() {
        if (J('input[name=32_1]').val()) {
          var a32_1 = parseFloat(J('input[name=32_1]').val())
        } else {
          var a32_1 = 0
        }
        if (J('input[name=33_1]').val()) {
          var a33_1 = parseFloat(J('input[name=33_1]').val())
        } else {
          var a33_1 = 0
        }
        let sum = a32_1 + a33_1
        J('input[name=31_1]').val(sum.toFixed(2))
      }
    }
  }
</script>

<style scoped>
  table, table tr th, table tr td {
    font-family: '宋体';
    border: 1px solid #333;
  }

  table {
    max-width: 900px;
    min-height: 25px;
    line-height: 25px;
    text-align: center;
    border-collapse: collapse;
    padding: 2px;
  }

  input[type=text], select {
    width: 100%;
    height: 100%;
    border: none;
    background: transparent;
    padding: 0 2px;
    outline: 0px;
    text-align: right;
  }

  td {
    padding: 3px 2px;
    text-align: left;
  }

  .bg-write {
    background: #d6ebff;
  }

  .bg-unwrite {
    background: #ccccff;
  }

  input[type=radio] {
    position: relative;
    top: 2.5px;
  }

  .bg-gray {
    background: #ddd;
  }

  .tdcon {
    text-align: center;
  }
</style>



